* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

img {
  width: 300px;
  height: 400px;
  padding: 20px;
  display: block;
}

.movie-list {
  flex-flow: wrap;
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin: 20px;
  list-style-type: none;
  display: flex;
}

.movie-card {
  border: 2px solid #bfc7bf;
  border-radius: 10px;
  box-shadow: 5px 15px 15px gray;
}

.movie-card:hover {
  transform: scale(1.05);
}

.movie-info {
  width: 300px;
  flex-flow: column wrap;
  padding: 20px;
  display: flex;
}

.movie-title {
  font-size: 18px;
  font-weight: bold;
}

.load-more-hidden {
  display: none;
}

.load-more {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background-color: #924343;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 1.2em;
  font-weight: bold;
  transition: background-color .3s ease-in-out, transform .2s ease-in-out;
  display: inline-block;
}

.btn-container {
  justify-content: center;
  display: flex;
}

